@import "colors.scss";

body {
    background-color: $primary-color; }

html, body {
    height: 100%; }


#signinBtn {
    width: 100%;
    background-color: #7EB9EC;
    box-shadow: 0 0 0 0 white!important;
    border-radius: .4rem;
    padding: 6px;
    height: 46px; }

#appName {
    color: white;
    font-size: 1.5rem; }

input {
    border: 0.1rem solid #d1d1d1;
    border-radius: 4px;
    box-shadow: none;
    height: 2.8rem;
    padding: 5px 15px;
    width: 100%;
    appearance: none; }

input:hover {
    border: 2px solid #3498db;
    outline: 0; }

.input-field {
    margin-top: 0!important; }

.container {
    padding-top: 5vh; }
